import Vue from 'vue';
import Vuex from 'vuex';
import a from './a';
import select from './select';

Vue.use(Vuex);

export default new Vuex.Store({
    // 
    state: {
        root: '',
    },
    // vuex  computed
    getters: {
        /**
         *
         * @param {*} state    store  state 
         * @param {*} getters  getters  getter 
         */
        hello(state, getters) {
            return state.root + '' + getters.cat;
        },
        cat() {
            return '';
        },
    },
    // vuex 
    mutations: {
        /**
         *
         * @param {*} state     store  state 
         * @param {*} paramter 
         */
        say(state, paramter) {
            console.log('');
        },
    },
    // vuex  Promise
    actions: {
        delaySay2(state) {
            return new Promise((reslove, reject) => {
                setTimeout(() => {
                    console.log('2.');
                    /**
                     * this  vuex  this.$store  vuex 
                     */
                    console.log(state, this);
                    reslove();
                }, 1000);
            });
        },
    },
    modules: {
        // key  store  namespaced 
        a,
        select,
    },
});
